import React, { useState } from "react";
import AlertDialog from "../components/Dialog";

import ribbon from './../assets/img/first-place-ribbon.png';

import axios from "axios";

import configApi from './../config/api';

function Login(props) {
  const [open, setOpen] = React.useState(false);

  const onSubmitLogin = (email, password) => {
    
    axios
      .post(`${configApi.BASE_URL}/login`, {
        email: email,
        password: password,
        appId: 1
      })
      .then((response) => {
        props.setToken(response.data);
      }).catch((err) => {
        setOpen(true)
      });
  }

  const [formData, setFormData] = useState({
    email: "",
    password: ""
  })


  return (
    <div className={`indie-font h-screen w-full grid content-center`}>
      <div className="relative h-[667px] bg-[#2E683E] w-[375px] mx-auto rounded-xl shadow-md overflow-hidden border-4 border-slate-50">
        <div className="mx-auto w-full h-[100px] text-center align-middle flex flex-row">
          <div className="flex justify-center items-center h-full w-full mx-auto">
            <img src={ribbon}
              width={48}
              height={48}
              alt="Logo Sinala Care"
              className="mr-5"
            />
            <div>
              <h1 className="text-3xl font-bold text-white">Sinala Care</h1>
              <p className={`text-md font-thin text-slate-200`}>simulator apotek</p>
            </div>
          </div>
        </div>
        <div className="absolute bottom-0 left-0 bg-slate-200 h-[567px] w-full rounded-t-xl">
          <div className="relative h-full rounded-xl">
            <div className="w-full h-full text-center">

              <h1 className="text-xl font-bold mt-4 text-gray-800/80">Sign In</h1>
              <div className="mt-4">
                <input className="w-[80%] shadow appearance-none border border-gray-500 focus:border-blue-500 rounded py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
                  id="email"
                  type="email"
                  placeholder="email"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                />
                {/* <p className="text-red-500 text-xs italic">Please choose a password.</p> */}
              </div>
              <div className="mb-6">
                <input className="w-[80%] shadow appearance-none border border-gray-500 focus:border-blue-500 rounded py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
                  id="password"
                  type="password"
                  placeholder="password"
                  value={formData.password}
                  onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                />
                {/* <p className="text-red-500 text-xs italic">Please choose a password.</p> */}
              </div>
              <button className="w-[80%] bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 border-b-4 border-blue-700 hover:border-blue-500 rounded"
                type="button"
                onClick={() => onSubmitLogin(formData.email, formData.password)}>
                Masuk
              </button>
            </div>
            <div className="absolute bottom-10 w-full text-center">
              <p className={`text-md font-thin text-slate-500`}>Sinala Care v-0.0.1</p>
              <p className={`text-sm font-thin text-slate-500`}>&copy; 2023 - Universitas Hang Tuah Surabaya</p>
            </div>
          </div>
        </div>
      </div>
      <AlertDialog open={open} setOpen={setOpen} title={'Oops'} description={'Data user tidak ditemukan'} />
    </div>
  );
}

export default Login;